
.G-fade-enter {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  opacity: 1;
}
.G-fade-enter-active {
  transition: all 0.18s cubic-bezier(0.27, 0.68, 0.51, 0.92);
}
.G-fade-leave-active {
  transition: all 0s cubic-bezier(0.27, 0.68, 0.51, 0.92);
}
.G-fade-leave,
.G-fade-enter-to {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.G-fade-enter-to {
  opacity: 1;
}
.G-fade-leave-to {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
  opacity: 1;
}


